@import "~@/scss/main.scss";

section {
  font-size: 14px;
  border-radius: $main-border-radius;
  box-shadow: 0px 0px 8px -5px var(--box-shadow-color);
  padding-bottom: 25px;
  background-color: var(--elements-color);
  cursor: pointer;
  overflow: hidden;
  img {
    width: 100%;
    height: 150px;
  }
  > div {
    padding: 20px;
    line-height: 2;

    h4 {
      color: var(--text-color);
      margin-bottom: 16px;
      font-size: 16px;
    }
    span:first-of-type {
      color: var(--text-color);
      font-weight: 600;
    }
    span:last-of-type {
      color: var(--inputs-color);
      font-weight: 600;
    }
  }
}
