@import "~@/scss/main.scss";

.details {
  @extend %main-page-style;
  button {
    padding: 9px 28px;
    color: var(--inputs-color);
    background-color: var(--elements-color);
    border: none;
    box-shadow: 0px 0px 6px -1px var(--box-shadow-color);
    border-radius: $main-border-radius;
    margin: 60px 0;
    svg {
      color: var(--text-color);
      margin-right: 10px;
    }
  }
  .main-content {
    display: flex;
    gap: 70px;
    @include underMedium {
      flex-direction: column;
      align-items: center;
      gap: 30px;
    }
    @include underTablet {
      align-items: start;
    }
    > img {
      width: 450px;
      filter: drop-shadow(0px 20px 0px var(--filter-color))
        drop-shadow(0px -20px 0px var(--filter-color))
        drop-shadow(20px 0px 0px var(--filter-color))
        drop-shadow(-20px 0px 0px var(--filter-color));
      @include underLarge {
        align-self: center;
      }
      @media (max-width: 500px) {
        width: 100%;
      }
    }
    > div {
      padding: 20px 0;
      flex: 1;
      h2 {
        color: var(--text-color);
      }
      > .main-info {
        display: flex;
        justify-content: space-between;
        @include underLarge {
          flex-direction: column;
        }
        > div {
          margin: 20px 0;
          display: flex;
          flex-direction: column;
          gap: 10px;
          > span {
            color: var(--text-color);
            font-weight: 600;
            > span {
              color: var(--inputs-color);
              &:before {
                content: " ";
              }
            }
          }
        }
      }
      > .borders {
        display: flex;
        gap: 15px;
        margin-top: 40px;
        @include underLarge {
          flex-direction: column;
        }
        @include underMedium {
          margin-top: 20px;
        }
        > span {
          white-space: nowrap;
          color: var(--text-color);
          font-weight: 600;
        }
        > div {
          display: flex;
          flex-wrap: wrap;
          gap: 8px 5px;
          span {
            color: var(--inputs-color);
            border-radius: $main-border-radius;
            background-color: var(--elements-color);
            box-shadow: 0px 0px 8px -4px var(--box-shadow-color);
            font-size: 14px;
            min-width: 70px;
            text-align: center;
            padding: 4px 6px;
          }
        }
      }
    }
  }
}
